<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process223"></a>

<p><br></p>
<p><span class="rvts17">强征入伍（</span><span class="rvts50">Press-Ganged</span><span class="rvts17">）</span><span class="rvts50">[</span><span class="rvts17">支线</span><span class="rvts50">]</span><span class="rvts17">：</span></p>
<p class="rvps2"><span class="rvts41">坐标：</span>
<br><span class="rvts20">X:534,Y:414</span><span class="rvts21">，</span><span class="rvts20">NPC</span><span class="rvts21">：卡宾</span><span class="rvts20"></span><span class="rvts21">戴（</span><span class="rvts20">Corbin Day</span><span class="rvts21">）</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">目标：</span>
<br><span class="rvts21">杀掉孤狼首领罗斯特，使卡宾从困境中解脱</span>
<br><span class="rvts20"></span>
<br><span class="rvts41">任务详情：</span>
<br><img height="auto" alt="" hspace="1" vspace="1" src="../../../assets/pic2/abcamber1287.jpg" display="block" max-width="100%"><span class="rvts39"></span>
<br><span class="rvts19">卡宾</span><span class="rvts20"></span><span class="rvts21">戴就在锯木厂的一处角落，由于被强征入伍，因此显得比较苦恼。尝试选择辨别出</span><span class="rvts20"></span><span class="rvts21">他与其它孤狼士兵</span><span class="rvts20"></span><span class="rvts21">不同的语句，他会偷偷告诉你，他想要逃出锯木厂。</span>
<br><span class="rvts21">在杀掉锯木厂头领罗斯特后，再次返回这里与卡宾对话并遣返他去复仇女神号。他在之后会在复仇女神号上出现。该</span><span class="rvts20">NPC</span><span class="rvts21">出售猎人技能书和较好的装备。</span>
<br><span class="rvts21">如果在击杀罗斯特之前未与卡宾进行</span><span class="rvts20"></span><span class="rvts21">逃跑计划</span><span class="rvts20"></span><span class="rvts21">的相关谈话，那么他很有可能不会跟随我们登上复仇女神号。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



